
.carousel-item {
    background: #3b3b3b;
}

.carousel-item img{
    object-fit: cover;
    object-position: center;
    height: 50vh;
    min-height: 500px;
    opacity: .5;

}

.jumbotron {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: transparent;
    color: #fff;
}

.results {
    position: absolute;
    left: 0;
    top: 110%;
    z-index: 10;
    width: 100%;
}

nav {
    background: transparent;
}